@import "../helpers/var";

.location {
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 2.5% 0 0;
    background: $blue url('../img/project__map.png') 90% center / 45% no-repeat;

    &--main {
        color: $black;
        padding: 2.5% 0;
        background: $white url('../img/map.png') right center / 50% no-repeat;

        .location__link-item {
            color: $black; }

        .footer-description__title {
            color: $black;
            margin: calc(0.25rem + 3.5vw) 0 0;
            font-size: calc(0.25rem + 1vw);
            line-height: calc(0.5rem + 1.25vw); }

        .footer-address__item {
            color: $black; }

        .contacts-link {
            &__caption,
            &__item {
                color: $black; } } }

    &--wrapper {
        width: 75%;
        display: flex;
        flex-direction: column;
        margin: 0 auto;
 }        // margin: 0 auto 0 15%

    &--margin {
        margin: 0 auto 0 12.5%;

        & .footer-description {
            width: 70%; } }

    &__title {
        font-size: calc(0.25rem + 2.25vw);
        line-height: calc(0.5rem + 2.25vw);
        width: 100%;
        display: flex;
        text-align: left; }

    &__link {
        margin: 1rem 0;
        &-item {
            text-decoration: underline;
            line-height: calc(0.5rem + 1.25vw);
            font-size: calc(0.25rem + 0.75vw);
            padding: calc(0.5rem + 0.5vw) 0;
            color: $white;

            &:hover {
                color: $light-blue; } } } }

.footer-description {
    display: flex;
    flex-direction: column;
    width: 40%;

    &__title {
        display: flex;
        width: 100%;
        font-size: calc(0.25rem + 1vw);
        line-height: calc(0.5rem + 1.5vw);
        font-weight: 600;
        text-transform: uppercase;
        color: $white; } }

.footer-address {
    display: flex;
    flex-direction: column;
    // width: 100%
    margin: calc(0.5rem + 1.75vw) 0;

    &__item {
        display: inline-block;
        font-size: calc(0.25rem + 0.75vw);
        line-height: calc(0.5rem + 1.25vw);
        color: $white; } }

.footer-contacts {
    display: flex;
    flex-direction: column;
    // width: 100%

    &__item {
        display: flex;
        width: inherit;
        flex-direction: column; } }

.contacts-link {
    &__caption {
        font-size: calc(0.25rem + 0.75vw);
        line-height: calc(0.5rem + 1.25vw);
        color: $white; }

    &__item {
        font-size: calc(0.25rem + 0.75vw);
        line-height: calc(0.5rem + 1.25vw);
        color: $white; } }

@media screen and ( max-width: 1224px) {
    .location {
        &--wrapper {
            width: 85%; }
        &--margin {
            width: 50%;
            & .footer-description {
                width: 100%; } } } }

@media screen and ( max-width: 768px) {
    .location {
        &__title {
            font-size: calc(0.25rem + 4vw);
            line-height: calc(0.5rem + 4vw); }

        &--main {
            .footer-description__title {
                font-size: calc(0.5rem + 1.25vw);
                line-height: calc(0.75rem + 1.5vw); } }

        &__link {
            &-item {
                line-height: calc(0.5rem + 1.5vw);
                font-size: calc(0.5rem + 1vw); } } }

    .footer-description {
        width: 75%;
        &__title {
            // font-size: calc(0.75rem + 1.25vw)
 } }            // line-height: calc(0.5rem + 1.5vw)

    .footer-address {
        &__item {
            font-size: calc(0.5rem + 1vw);
            line-height: calc(0.5rem + 1.5vw); } }

    .contacts-link {
        &__caption {
            font-size: calc(0.5rem + 1vw);
            line-height: calc(0.5rem + 2vw); }

        &__item {
            font-size: calc(0.5rem + 1vw);
            line-height: calc(0.5rem + 2vw); } } }

@media screen and ( max-width: 675px) {
    .location {
        background-image: none;
        &--wrapper {
            align-items: center; } }

    .footer-description {
        display: grid;
        grid-template-columns: repeat(2, 1fr);

        flex-direction: row;
        width: 100%;

        &__title {
            text-align: center;
            justify-content: center;
            align-items: center;
            grid-column: 1/3; } }

    .footer-address {
        grid-column: 2/3;
        grid-row: 2/3;
        align-items: flex-end; }
    .footer-contacts {
        grid-column: 1/2;
        grid-row: 2/3;
        margin: calc(0.5rem + 1.75vw) 0; } }
