@import "../helpers/var";

.blog {
    display: flex;
    width: 100%;
    padding: 10% 0 0;

    &--wrapper {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        width: 75%;
        margin: 0 auto; }

    &__item {
        display: flex;
        width: 100%; } }

.blog-description {
    flex-direction: column;
    justify-content: space-evenly;
    padding: 15% 10% 15% 0;
    &__title {
        font-size: calc(0.25rem + 2.25vw);
        width: 100%;
        display: inline-block;
        padding: 0 0 2.5%;
        line-height: calc(0.5rem + 3vw); }

    &__caption {
        display: inline-block;
        font-size: calc(0.25rem + 1.25vw);
        width: 100%;
        padding: calc(0.5rem + 0.5vw) 0;
        font-weight: 600;
        line-height: calc(0.5rem + 1.5vw); }

    &__legend {
        margin: calc(0.5rem + 0.5vw) 0;
        display: inline-block;
        font-size: calc(0.25rem + 0.75vw);
        line-height: calc(0.5rem + 1.25vw);
        text-align: justify;
        width: 100%; }

    &__link {
        display: inline-block;
        margin: calc(0.5rem + 0.5vw) 0;
        padding: calc(0.5rem + 0.5vw);
        border: 1px solid $light-blue;
        width: 35%;
        text-align: center;
        line-height: calc(0.5rem + 0.75vw);
        font-size: calc(0.25rem + 0.6vw); } }

.blog-img {
    text-align: right;
    justify-content: center;

    &__link {
        width: 100%;
        height: 100%; } }

@media screen and ( max-width: 1224px) {
    .blog {
        &--wrapper {
            width: 85%; } } }
