@import "./src/sass/helpers/var";

.header {
  width: 75%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: rgba($white, 1);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  padding: 0 2.5%;
  top: 5%;
  transition: all 0.3s ease;

  &--fixed {
    top: 0;
    width: 100%; }

  &--adaptive {
    width: 100%;
    top: 0%;
    transition: all 0.3s ease-in-out; } }

@media screen and (max-width: 1366px) {
  .header {
    width: 85%;

    &--fixed {
      width: 100%; } } }

@media screen and ( max-width: 1024px) {
  .header {
    width: 100%;
    top: 0; } }

